body {
  background-image: linear-gradient(135deg, #fdeb71 10%, #f8d800 100%);
}

.all-wrapper {
  margin: 12vh auto;
  padding: 50px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 70vw;
  height: 63vh;
  background: #fff;
  text-align: center;
  border-radius: 30px;
  overflow: auto;

  box-shadow: 0px 10px 2px rgba(0, 0, 0, 0.14);
}

input {
  margin: auto;
  width: 100px;
}
